
let DhProperties = {
    properties:{
        staticImageBasePath: ""
    }
}

function imagePath(name) {
    if(name){
    	return (DhProperties.properties.staticImageBasePath + name);
    }
    return "";
}

function isValidImagePath(imgPath){
    if(typeof(imgPath)!=="undefined"){
        return (imgPath.endsWith(".jpg") || imgPath.endsWith(".jpeg") || imgPath.endsWith(".png") || imgPath.endsWith(".webp"));
    }
    return false;
}

function getDefaultImage(product){
    // console.log("image failed: ", product);
    switch(product){
        case "mobile":
            return imagePath("mobile-NoImage.jpg");
        case "laptop":
            return imagePath("laptop-NoImage.jpg");
        case "smart-tv":
            return imagePath("smart-tv-NoImage.png");
        default:
            return null;
    }
}

function scaleDownImage(url, factor, limitDimension){
     // limitDimension (optional) will gurantee minimum possible value of width and height even after scaling. 
    if(typeof(url)!=='string' || isNaN(factor)){
        return url;
    }
    let resizedImgUrl = url;
    try{
      resizedImgUrl = resizedImgUrl.replace(/\/img\/[0-9]+x[0-9]+x[0-9]+\/img\//, (match)=>{
          let size = match.replace(/\/img\//g,'').split('x').map((el,i)=>(i==1)?el:Math.ceil(el/factor));
          if(size.length<3){return url;}
          if(limitDimension && limitDimension.minWidth && limitDimension.minHeight && !isNaN(limitDimension.minWidth) && !isNaN(limitDimension.minHeight)){
              size[0]  = Math.max(size[0], limitDimension.minWidth); // min-width: 
              size[2]  = Math.max(size[2], limitDimension.minHeight); // min-height: 
          }
          size = size.join('x');
          return `/img/${size}/img/`;
        });
    }catch(err){
        console.log(err);
    }
    return resizedImgUrl;
}

export {DhProperties, imagePath, isValidImagePath, getDefaultImage, scaleDownImage};


