import React, { Component, Fragment } from 'react';

import PropTypes from 'prop-types';
import { inject, observer} from 'mobx-react';
import AppStore from '../../store/AppStore8';
import loadable from '@loadable/component';
import { LazyRenderer, EmptyComponent } from './../../utility/lazyRenderer';

import Page8Card1 from './subComponent/page8/page8card1';



@inject('appStore')
@observer
class LandingPage8 extends Component {
  constructor(props) {
    super(props);
}

static propTypes = {
    appStore: PropTypes.object.isRequired,
};

static defaultProps = {
    appStore: new AppStore(),
};

  componentDidMount(){
    if(typeof(landingPageDidMount)!=="undefined"){
      landingPageDidMount=true;
    }
  }
  
  render() {
    const { data } = this.props.appStore;
    return (
      <div>
        {!data.cards[0].loadError && <Page8Card1 data={data.cards[0]} lang={data.lang} appStore={this.props.appStore}></Page8Card1>}

      </div>
    )
  }
}

export default LandingPage8;
