import React from "react";
import ReactDOM from "react-dom";
import { Provider } from 'mobx-react'; 
import LandingPage from "./project/LandingPage8";
import AppStore from "../store/AppStore8";
import { DhProperties } from "../DhProperties";

DhProperties.properties = window.__ENV_PROPERTIES_ISOMORPHIC__;
const appStore = new AppStore(window.__INITIAL_STATE__);

ReactDOM.hydrate(
    <Provider appStore={appStore}>
        <LandingPage></LandingPage>
    </Provider>,
        
    document.querySelector('#root')
);
